<template>
  <div id="app">
    <Headers v-if="$route.meta.navShow"></Headers>
    <!-- <div>{{LocationCity}}</div> -->
    <keep-alive v-if="$route.meta.keepAlive">
      <router-view></router-view>
    </keep-alive>
    <keep-alive v-if="!$route.meta.keepAlive">
      <transition name="fadeIn" mode="in-out">
        <router-view ref="mfView"></router-view>
      </transition>
    </keep-alive>
  </div>
</template>

<script>
// import BMap from 'BMap';
import Headers from "./components/Header";
import SortCard from "./components/SortCard";
export default {
  name: "App",
  data() {
    return {
      LocationCity: "正在定位"
    };
  },
  mounted() {
    // this.city();
  },
  created() {},
  methods: {
    city() {
      //定义获取城市方法
      const geolocation = new BMap.Geolocation();
      var _this = this;
      geolocation.getCurrentPosition(
        function getinfo(position) {
          let city = position.address.city; //获取城市信息
          let province = position.address.province; //获取省份信息
          _this.LocationCity = city;
        },
        function(e) {
          _this.LocationCity = "定位失败";
        },
        { provider: "baidu" }
      );
    }
  },
  components: {
    Headers,
    SortCard
  }
};
</script>

<style>
* {
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Helvetica",
    "Arial", "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei",
    "WenQuanYi Micro Hei", "sans-serif";
}
a {
  color: #000;
}

.fadeIn-enter-active,
.fadeIn-leave-active {
  height: 100vh;
  will-change: transform;
  transition: all .4s;
  overflow: hidden;
}
.fadeIn-enter {
  /* 进入动画 */
  opacity: .8;
  transform: scale(0.75);
}
.fadeIn-leave-active {
  /* 离开动画 */
  position: fixed;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  opacity: 0;
  transform: scale(0.75);
}
</style>



